.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@font-face {
font-family: 'PIRATE SCROLL Regular';
font-style: normal;
font-weight: normal;
src: local('PIRATE SCROLL Regular'), url('Pirate Scroll.woff') format('woff');
}

h1, h2 {
  font-family: 'PIRATE SCROLL Regular';
  color : goldenrod;
}

p {
  font-family: arial;
  font-size:  2em;
  color:  blue;
}